"use client";

import Link from "next/link";
import { useUser, SignInButton, UserButton } from "@clerk/nextjs";
import { Button } from "@/components/ui/button";
import { ThemeToggle } from "@/components/theme-toggle";
import { Menu, X, MessageSquare } from "lucide-react";
import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

const navigationItems = [
  { href: "/features", label: "Features" },
  { href: "/rules", label: "Rules" },
  { href: "/leaderboard", label: "Leaderboard" },
  { href: "/events", label: "Events" },
  { href: "/status", label: "Status" },
] as const;

export function Navbar() {
  const { isSignedIn } = useUser();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  return (
    <header className="sticky top-0 z-50 w-full border-b border-border/80 bg-background/90 backdrop-blur-md supports-[backdrop-filter]:bg-background/70">
      <div className="container flex h-16 items-center justify-between">
        {/* Left: Logo */}
        <div className="flex items-center gap-4">
          <Link href="/" className="flex items-center gap-2 transition-opacity hover:opacity-90">
            <span className="gradient-text pl-1 text-xl font-bold tracking-tight md:text-2xl">
              AevumMC
            </span>
          </Link>
        </div>

        {/* Center: Navigation (desktop only) */}
        <nav className="hidden md:flex items-center gap-6 text-sm font-medium">
          {navigationItems.map((item) => (
            <Link
              key={item.href}
              href={item.href}
              className="nav-link-underline py-2 text-muted-foreground transition-colors hover:text-foreground rounded px-2"
            >
              {item.label}
            </Link>
          ))}
        </nav>

        {/* Right: Utility buttons */}
        <div className="flex items-center gap-2">
          {/* Discord */}
          <Link href="/discord" aria-label="Discord">
            <Button
              variant="ghost"
              size="icon"
              className="size-9 text-muted-foreground hover:text-foreground"
              tabIndex={0}
            >
              <MessageSquare className="size-4" />
            </Button>
          </Link>

          {/* Theme toggle */}
          <ThemeToggle />

          {/* Desktop login/dashboard buttons */}
          {isSignedIn ? (
            <>
              <Link href="/dashboard">
                <Button
                  variant="outline"
                  size="sm"
                  className="hidden md:inline-flex font-medium"
                >
                  Dashboard
                </Button>
              </Link>
              <UserButton
                afterSignOutUrl="/"
                appearance={{
                  elements: {
                    userButtonAvatarBox: "size-8",
                    userButtonPopoverCard: "rounded-lg",
                  }
                }}
              />
            </>
          ) : (
            <SignInButton mode="modal">
              <Button
                variant="outline"
                size="sm"
                className="hidden md:inline-flex font-medium"
              >
                Sign In
              </Button>
            </SignInButton>
          )}

          {/* Hamburger menu button (mobile only) */}
          <button
            type="button"
            className="inline-flex size-10 items-center justify-center rounded-lg text-muted-foreground transition-colors hover:bg-accent hover:text-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring md:hidden"
            onClick={() => setMobileMenuOpen((v) => !v)}
            aria-expanded={mobileMenuOpen}
            aria-label="Open mobile navigation menu"
          >
            {mobileMenuOpen ? <X className="size-5" /> : <Menu className="size-5" />}
            <span className="sr-only">Toggle menu</span>
          </button>
        </div>
      </div>

      {/* Mobile menu dropdown */}
      <AnimatePresence>
        {mobileMenuOpen && (
          <motion.div
            initial={{ height: 0, opacity: 0 }}
            animate={{ height: "auto", opacity: 1 }}
            exit={{ height: 0, opacity: 0 }}
            transition={{ duration: 0.18 }}
            className="overflow-hidden border-b border-border/80 bg-background/95 backdrop-blur-md md:hidden rounded-b-xl shadow-lg"
          >
            <nav className="container flex flex-col gap-1 py-4">
              {navigationItems.map((item, i) => (
                <motion.div
                  key={item.href}
                  initial={{ opacity: 0, x: -8 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ delay: i * 0.045 }}
                >
                  <Link
                    href={item.href}
                    className="block rounded-lg px-3 py-2.5 text-sm font-medium text-muted-foreground transition-colors hover:bg-accent hover:text-foreground"
                    onClick={() => setMobileMenuOpen(false)}
                  >
                    {item.label}
                  </Link>
                </motion.div>
              ))}
              <div className="flex gap-2 pt-3">
                <Link href="/discord" aria-label="Discord">
                  <Button
                    variant="ghost"
                    size="icon"
                    className="size-8 text-muted-foreground hover:text-foreground"
                    tabIndex={0}
                  >
                    <MessageSquare className="size-4" />
                  </Button>
                </Link>
                <ThemeToggle />
                {isSignedIn ? (
                  <>
                    <Link href="/dashboard" tabIndex={0}>
                      <Button
                        variant="outline"
                        size="sm"
                        className="font-medium"
                        onClick={() => setMobileMenuOpen(false)}
                      >
                        Dashboard
                      </Button>
                    </Link>
                    <UserButton
                      afterSignOutUrl="/"
                      appearance={{
                        elements: {
                          userButtonAvatarBox: "size-7",
                          userButtonPopoverCard: "rounded-lg",
                        }
                      }}
                    />
                  </>
                ) : (
                  <SignInButton mode="modal">
                    <Button
                      variant="outline"
                      size="sm"
                      className="font-medium"
                      onClick={() => setMobileMenuOpen(false)}
                    >
                      Sign In
                    </Button>
                  </SignInButton>
                )}
              </div>
            </nav>
          </motion.div>
        )}
      </AnimatePresence>
    </header>
  );
}